@charset "UTF-8";
/* <!-- ---------- Header - Sección LAURA ---------- --> */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style-type: none;
  text-decoration: none; }

:root {
  --text-color: #14d9c4;
  --blue-green: #099d8d;
  --dark-grey: #54585a;
  --Rubik-font: "Rubik", sans-serif; }

body {
  font-family: var(--Rubik-font);
  /* min-width: 320px; */ }

.header__base {
  height: 60px;
  width: 100%;
  background-color: #099d8ecf;
  opacity: 0.95;
  display: flex;
  justify-content: center;
  align-items: center; }

section.header__base h1 {
  color: white;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.88;
  letter-spacing: normal; }

.rectangle {
  height: 60px;
  width: 100%;
  background-color: #14d9c4;
  opacity: 0.9;
  font-family: Rubik, sans-serif;
  font-size: 15px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 2;
  letter-spacing: normal;
  list-style: none;
  display: flex;
  justify-content: space-around;
  align-items: center; }

.item-menu {
  color: white;
  text-transform: uppercase; }

.sticky {
  width: 100%;
  z-index: 100;
  position: fixed; }

/* <!-- ---------- Header - Sección LAURA ---------- --> */
/*------------------------hero seccion maria*-------------------------*/
.hero {
  width: 100%;
  height: 500px;
  background-color: thistle; }

.hero-img {
  width: 100%;
  height: 100%;
  object-fit: cover; }

.rectangle-2 {
  position: relative;
  width: 290px;
  height: 80px;
  top: -5%;
  left: 50%;
  transform: translate(-50%, 0);
  padding: 15px;
  background-color: white;
  text-align: center; }

.lema {
  width: 100%;
  padding-top: 10px;
  padding-left: 10px;
  font-family: Rubik;
  font-size: 20px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.39;
  letter-spacing: normal;
  color: var(--black); }

/* <!-- ---------- MARTA ---------- --> */
.team {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 60px;
  width: 100%;
  margin-bottom: 30px; }

.EQUIPO {
  box-sizing: border-box;
  text-align: center;
  width: 100%;
  height: 45px;
  margin-left: 15px;
  margin-right: 15px;
  margin-top: 30px;
  font-family: var(--Rubik-font);
  font-size: 24px;
  font-weight: 500;
  line-height: 1.88;
  color: var(--text-color);
  scroll-margin-top: 150px; }

.lorem {
  box-sizing: border-box;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: "Open Sans", sans-serif;
  color: #54585a;
  font-size: 16px;
  line-height: 1.5; }

.FYD {
  box-sizing: border-box;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 600px;
  padding: 30px 15px 45px;
  background-color: #f1f1f1;
  color: var(--strengths-descriptions);
  line-height: 24px; }

.cuadrado {
  box-sizing: border-box;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  background-color: #fff;
  margin-left: auto;
  margin-right: auto;
  transform: translate(0, 50%) rotate(45deg); }

.h3 {
  color: var(--blue-green);
  font-family: var(--Rubik-font);
  font-size: 22px;
  font-weight: 500;
  line-height: 2.05; }

.parrafo {
  width: 290px;
  height: 192px;
  font-family: "Open Sans", sans-serif;
  color: #54585a; }

.parrafo2 {
  width: 290px;
  height: 192px;
  margin: 75px 0 0;
  font-size: 16px;
  font-family: "Open Sans", sans-serif;
  line-height: 1.5;
  color: #54585a; }

/* <!-- ----------fIN MARTA ---------- --> */
/* ========== Quiénes somos - Sección ICO ==========
  ============= Quiénes somos - Sección ICO ========== */
.container {
  margin: 0 15px; }

#about-us {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.quienes-somos {
  font-size: 24px;
  color: #099d8d;
  text-align: center;
  font-family: "Rubik", sans-serif;
  font-weight: 500;
  text-transform: uppercase;
  margin: 40px 0px 20px 0px;
  scroll-margin-top: 150px; }

.presentacion {
  display: flex;
  flex-direction: column;
  align-items: center; }

.profile-pic {
  width: 180px;
  height: 180px;
  border-radius: 4px;
  border: solid 5px #f1f1f1;
  margin: 10px;
  object-fit: cover;
  object-position: top; }

.presentacion__text {
  display: flex;
  flex-direction: column;
  align-items: center; }

.my-name {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 20px;
  text-align: center;
  margin-top: 10px; }

.about-me {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  text-align: center;
  color: #54585a;
  margin: 10px 0px; }

.social {
  margin-top: 20px;
  margin-bottom: 40px; }

.social-icon {
  font-family: "Open Sans", sans-serif;
  display: inline;
  font-size: 16px;
  margin: 6px;
  color: var(--blue-green); }

.social-icon a,
.dot,
.link-rrss {
  color: var(--blue-green); }

/* ========== Fin Sección ICO ==========
  ============= Fin Sección ICO ========== */
/* <!-- ---------- Footer - Sección VICTORIA---------- --> */
@media all and (min-width: 768px) {
  /* <!-- ---------- Header ---------- --> */
  .sticky {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #099d8e;
    opacity: 0.95; }
  .header__base {
    height: 60px;
    width: 50%;
    display: flex;
    justify-content: flex-start;
    padding: 15px 0px 15px 45px;
    background-color: #099d8e00; }
  .rectangle {
    height: 60px;
    width: 100%;
    background-color: #099d8e00;
    padding: 15px 45px 15px 0px; }
  .item-menu {
    padding: 0px 0px 0px 18px; }
  /* <!-- ---------- Header ---------- --> */
  /* <!-- ---------- Quiénes somos (Ico) ---------- --> */
  .container {
    margin: 45px; }
  /* <!-- ---------- Quiénes somos (Ico) ---------- --> */
  .footer1 {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding: 25px;
    width: 100%;
    height: 170px; }
  .logo {
    flex-direction: row;
    height: 45px;
    width: 115.9;
    margin: 20px; }
  .Nombre-del-grupo-4 {
    width: 172px; } }
